import { useEffect, useState } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import {
  Button,
  FormControl,
  Box,
  FormHelperText,
  Link,
  Stack,
} from "@mui/material";
import { Lock, LockOpen, Person } from "@mui/icons-material";
import AuthService from "~/services/AuthService";
import styles from "./Login.module.css";
import { PROCESSED_ERRORS, onApiError } from "~/utils/errorsProcessor";
import { GoogleLogin, GoogleOAuthProvider } from "@react-oauth/google";
import { useDispatch } from "react-redux";
import { onError, setLoading } from "~/state/edit/editState";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import SignUp from "../Signup";
import LoginInput from "~/components/common/LoginInput";
function Login() {
  const { t } = useTranslation("manage");
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const location = useLocation();
  const [password, setPassword] = useState("");
  const [passError, setPassError] = useState("");
  const [email, setEmail] = useState("");
  const [emailError, setEmailError] = useState("");
  const [value, setValue] = useState(0);
  const loginPath = "/login";
  const signupPath = "/signup";

  useEffect(() => {
    const path = location.pathname;
    if (path === signupPath) {
      setValue(1);
    } else if (path === loginPath) {
      setValue(0);
    }
  }, [location]);
  const handleChangeTabs = (event, newValue) => {
    setValue(newValue);
    if (newValue === 0) {
      navigate(loginPath);
    } else if (newValue === 1) {
      navigate(signupPath);
    }
  };

  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword((show) => !show);

  const validate = () => {
    setEmailError("");
    setPassError("");
    if (email.length == 0) {
      setEmailError(t("email_required"));
      return false;
    }
    if (password.length == 0) {
      setPassError(t("password_required"));
      return false;
    }
    return true;
  };

  const onPasswordChanged = (e) => {
    setPassword(e.target.value);
    setPassError("");
  };

  const onEmailChanged = (e) => {
    setEmail(e.target.value);
    setEmailError("");
  };

  useEffect(() => {
    const input = document.querySelector('input[name="email"]');
    if (input && input.value !== email) {
      setEmail(input.value);
    }
  }, [email]);

  const handleError = (e) => {
    onApiError({
      error: e,
      globalErrorHandler: (processedError) => {
        dispatch(onError(processedError));
      },
      locallErrorHandler: (processedError) => {
        if (processedError.name == PROCESSED_ERRORS.WRONG_CREDENTIALS.name) {
          setEmailError(t(`processed_errors.${processedError.name}`));
          setPassError(t(`processed_errors.${processedError.name}`));
        }
      },
    });
  };

  const onSubmit = () => {
    if (!validate()) {
      return;
    }
    dispatch(setLoading(true));
    AuthService.login(email, password)
      .then((data) => {
        const { from } = location.state || { from: { pathname: "/" } };
        navigate(from);
      })
      .catch((e) => {
        handleError(e);
      })
      .finally(() => {
        dispatch(setLoading(false));
      });
  };

  const onGoogleAuthSuccess = (credentialResponse) => {
    dispatch(setLoading(true));
    AuthService.googleSignIn(credentialResponse)
      .then((data) => {
        const { from } = location.state || { from: { pathname: "/" } };
        navigate(from);
      })
      .catch((e) => {
        handleError(e);
      })
      .finally(() => {
        dispatch(setLoading(false));
      });
  };

  return (
    <Box
      sx={{
        display: "flex",
        justifyContent: "center",
        alignItems: "start",
        height: "calc(100vh - 80px)",
        overflow: "auto",
        width: "100vw",
        backgroundColor: "#f7f8fc",
      }}
    >
      <Box sx={{ width: "100%", maxWidth: 700, marginTop: 5 }}>
        <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
          <Tabs
            value={value}
            onChange={handleChangeTabs}
            aria-label="basic tabs example"
            variant="fullWidth"
            classes={{ indicator: styles.tabIndicator }}
            TabIndicatorProps={{ style: { display: "none" } }}
            sx={{
              ".MuiTabs-indicator": {
                display: "none",
              },
              ".MuiButtonBase-root": {
                bgcolor: "#dfe2ef",
                color: "#16205b",
                textTransform: "none",
                padding: "25px",
                fontSize: "1.125rem",
                fontWeight: "600",
              },
              ".MuiButtonBase-root:nth-of-type(1)": {
                borderTopLeftRadius: "15px",
              },
              ".MuiButtonBase-root:nth-of-type(2)": {
                borderTopRightRadius: "15px",
              },
              ".Mui-selected": {
                bgcolor: "#fff",
                color: "#16205b !important",
              },
            }}
          >
            <Tab
              classes={{ root: styles.tabRoot, selected: styles.tabSelected }}
              label={t("login.title")}
            />
            <Tab
              className={value === 0 ? styles.selectedTab : styles.tab}
              label={t("sign_up.title")}
            />
          </Tabs>
        </Box>
        {value === 0 && (
          <Box className={styles.content}>
            <Box>
              <LoginInput
                label="Email"
                onChange={onEmailChanged}
                error={emailError?.length > 0}
                value={email}
                name="email"
                required
                variant="standard"
                icon={<Person sx={{ color: "#16205b" }} />}
              />

              <FormControl
                required={true}
                variant="standard"
                sx={{ mt: "10px" }}
              >
                <LoginInput
                  label={t("label.password")}
                  onChange={onPasswordChanged}
                  error={passError?.length > 0}
                  value={password}
                  type={showPassword ? "text" : "password"}
                  name="password"
                  required
                  variant="standard"
                  icon={
                    showPassword ? (
                      <LockOpen
                        onClick={handleClickShowPassword}
                        sx={{ color: "#16205b", cursor: "pointer" }}
                      />
                    ) : (
                      <Lock
                        onClick={handleClickShowPassword}
                        sx={{ color: "#16205b", cursor: "pointer" }}
                      />
                    )
                  }
                />
              </FormControl>
              {(emailError || passError) && (
                <FormHelperText className={styles.errorText}>
                  {emailError || passError}
                </FormHelperText>
              )}
              <Stack
                justifyContent="flex-end"
                flexDirection="row"
                className={styles.links}
                alignItems="center"
              >
                <Link
                  sx={{
                    color: "grey",
                    cursor: "pointer",
                    textDecoration: "none",
                  }}
                  onClick={() => {
                    navigate("/forgot-password");
                  }}
                >
                  {t("login.forgot_password")}
                </Link>
              </Stack>
              <Button
                fullWidth
                onClick={onSubmit}
                className={styles.loginButton}
                sx={{ mt: "25px" }}
              >
                {t("login.login")}
              </Button>
            </Box>
            <div style={{ width: "200px", margin: "auto" }}>
              <GoogleOAuthProvider
                clientId={import.meta.env.VITE_GOOGLE_AUTH_CLIENT_ID}
              >
                <GoogleLogin
                  onSuccess={onGoogleAuthSuccess}
                  onError={() => {
                    console.log("Login Failed");
                  }}
                />
              </GoogleOAuthProvider>
            </div>
          </Box>
        )}
        {value === 1 && <SignUp />}
      </Box>
    </Box>
  );
}

export default Login;
