import { useState } from "react";
import {
  Button,
  FormControl,
  Box,
  FormHelperText,
  FormGroup,
} from "@mui/material";
import { CheckCircle, Lock, LockOpen } from "@mui/icons-material";
import AuthService from "~/services/AuthService";
import styles from "./Signup.module.css";
import { useTranslation } from "react-i18next";
import { PROCESSED_ERRORS, onApiError } from "~/utils/errorsProcessor";
import { GoogleLogin, GoogleOAuthProvider } from "@react-oauth/google";
import { useNavigate } from "react-router-dom";
import { setLoading } from "~/state/edit/editState";
import { useDispatch } from "react-redux";
import LoginInput from "~/components/common/LoginInput";

function SignUp() {
  const { t } = useTranslation("manage");
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [isSuccess, setSuccess] = useState(false);

  const [email, setEmail] = useState("");
  const [emailError, setEmailError] = useState("");
  const [duplicateEmailError, setDuplicateEmailError] = useState("");

  const [firstName, setFirstName] = useState("");
  const [firstNameError, setFirstNameError] = useState("");

  const [lastName, setLastName] = useState("");
  const [lastNameError, setLastNameError] = useState("");

  const [password, setPassword] = useState("");
  const [passError, setPassError] = useState("");

  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword((show) => !show);

  const onPasswordChanged = (e) => {
    setPassword(e.target.value);
    setPassError("");
  };

  const onEmailChanged = (e) => {
    setEmail(e.target.value);
    setEmailError("");
  };

  const onFirstNameChanged = (e) => {
    setFirstName(e.target.value);
    setFirstNameError("");
  };

  const onLastChanged = (e) => {
    setLastName(e.target.value);
    setLastNameError("");
  };

  const validate = () => {
    setEmailError("");
    setPassError("");
    setFirstNameError("");
    setLastNameError("");
    if (firstName.length == 0) {
      setFirstNameError(t("firstname_required"));
      return false;
    }
    if (lastName.length == 0) {
      setLastNameError(t("lastname_required"));
      return false;
    }
    if (email.length == 0) {
      setEmailError(t("email_required"));
      return false;
    }
    if (password.length == 0) {
      setPassError(t("password_required"));
      return false;
    }
    return true;
  };

  const onGoogleAuthSuccess = (credentialResponse) => {
    dispatch(setLoading(true));
    AuthService.googleSignIn(credentialResponse)
      .then((data) => {
        const { from } = location.state || { from: { pathname: "/" } };
        navigate(from);
      })
      .catch((e) => {
        onError(e);
      })
      .finally(() => {
        dispatch(setLoading(false));
      });
  };

  const onSubmit = () => {
    if (!validate()) {
      return;
    }
    dispatch(setLoading(true));
    AuthService.signup({ email, password, firstName, lastName })
      .then(() => {
        setSuccess(true);
      })
      .catch((e) => {
        onApiError({
          error: e,
          globalErrorHandler: (processedError) => {
            dispatch(onError(processedError));
          },
          locallErrorHandler: (processedError) => {
            if (processedError.name == PROCESSED_ERRORS.DUPLICATE_EMAIL.name) {
              setDuplicateEmailError(
                t(`processed_errors.${processedError.name}`)
              );
            }
          },
        });
      })
      .finally(() => {
        dispatch(setLoading(false));
      });
  };

  return (
    <Box className={styles.wrapper}>
      <Box className={styles.content}>
        {isSuccess ? (
          <Box className={styles.successText}>
            <CheckCircle sx={{ fontSize: "60px" }} />
            {t("sign_up.success")}
          </Box>
        ) : (
          <Box className={styles.form}>
            <FormGroup className={styles.formGroup}>
              <LoginInput
                error={firstNameError.length > 0}
                required={true}
                value={firstName}
                id="firstName"
                label={t("label.first_name")}
                name="firstName"
                onChange={onFirstNameChanged}
                variant="standard"
              />
              {firstNameError && (
                <FormHelperText className={styles.errorText}>
                  {firstNameError}
                </FormHelperText>
              )}

              <LoginInput
                error={lastNameError?.length > 0}
                id="lastName"
                value={lastName}
                helperText={lastNameError}
                required={true}
                onChange={onLastChanged}
                label={t("label.last_name")}
                name="lastName"
                variant="standard"
              />
              {lastNameError && (
                <FormHelperText className={styles.errorText}>
                  {lastNameError}
                </FormHelperText>
              )}
              <LoginInput
                required={true}
                value={email}
                error={emailError?.length > 0 || duplicateEmailError.length > 0}
                id="email"
                onChange={onEmailChanged}
                label={t("label.email")}
                name="email"
                variant="standard"
              />
              {emailError ||
                (duplicateEmailError && (
                  <FormHelperText className={styles.errorText}>
                    {emailError || duplicateEmailError}
                  </FormHelperText>
                ))}

              <FormControl
                required={true}
                variant="standard"
                sx={{ mt: "10px" }}
              >
                <LoginInput
                  label={t("label.password")}
                  error={passError?.length > 0}
                  required={true}
                  value={password}
                  onChange={onPasswordChanged}
                  name="password"
                  variant="standard"
                  id="standard-adornment-password"
                  type={showPassword ? "text" : "password"}
                  icon={
                    showPassword ? (
                      <LockOpen
                        onClick={handleClickShowPassword}
                        sx={{ color: "#16205b", cursor: "pointer" }}
                      />
                    ) : (
                      <Lock
                        onClick={handleClickShowPassword}
                        sx={{ color: "#16205b", cursor: "pointer" }}
                      />
                    )
                  }
                />
                {passError && (
                  <FormHelperText className={styles.errorText}>
                    {passError}
                  </FormHelperText>
                )}
              </FormControl>

              <FormHelperText sx={{ fontSize: "10px" }}>
                {t("sign_up.helper_text")}
              </FormHelperText>
            </FormGroup>
            <Button
              fullWidth
              onClick={onSubmit}
              className={styles.signUpButton}
              sx={{ mt: "25px" }}
            >
              {t("sign_up.sign_up")}
            </Button>
          </Box>
        )}

        <div style={{ width: "200px", margin: "auto" }}>
          <GoogleOAuthProvider
            clientId={import.meta.env.VITE_GOOGLE_AUTH_CLIENT_ID}
          >
            <GoogleLogin
              onSuccess={onGoogleAuthSuccess}
              onError={() => {
                console.log("Login Failed");
              }}
            />
          </GoogleOAuthProvider>
        </div>
      </Box>
    </Box>
  );
}

export default SignUp;
